import { Link, NavLink, useParams } from 'react-router-dom';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';
import { getSite, slugify } from '../data/sites';

const SECTION_CONFIG = [
  { key: 'tos', label: 'Terms of Service' },
  { key: 'privacy', label: 'Privacy Policy' },
];

const TOS_SECTIONS = [
  { key: 'userRights', label: 'User rights' },
  { key: 'userAccounts', label: 'User accounts' },
  { key: 'intellectualProperty', label: 'Intellectual property' },
  { key: 'governingLaw', label: 'Governing laws' },
  { key: 'externalLinks', label: 'Links to other websites' },
  { key: 'usageLimits', label: 'Usage limits' },
  { key: 'trademarks', label: 'Trademarks' },
  { key: 'asIs', label: '“AS IS” disclaimer' },
  { key: 'acceptableUse', label: 'Acceptable usage' },
  { key: 'additionalProvisions', label: 'Additional provisions' },
];

const PRIVACY_SECTIONS = [
  { key: 'dataCollector', label: 'Data collector information' },
  { key: 'informationTypes', label: 'Type of information collected' },
  { key: 'informationUse', label: 'How information is used' },
  { key: 'securityMeasures', label: 'Security measures' },
  { key: 'retention', label: 'Data retention' },
  { key: 'sharing', label: 'Data sharing' },
  { key: 'userRights', label: 'User rights & controls' },
  { key: 'cookies', label: 'Cookie information' },
  { key: 'contact', label: 'Contact information' },
];

export default function SiteDetailPage() {
  const { slug, section } = useParams();
  const site = getSite(slug);
  const activeSection = section === 'privacy' ? 'privacy' : 'tos';

  if (!site) {
    return (
      <div className="space-y-8">
        <header className="space-y-3">
          <h1 className="text-3xl font-semibold text-white">Sites not found</h1>
          <p className="text-sm text-slate-400">
            Sorry. However, we couldn&apos;t find a summary for that website. The policies may have been removed or moved. Please took note that this is an independent project, so it may take us some times to add new policies to our website, and we are trying our best to increase the speed too.
          </p>
        </header>
        <Link
          to="/directory"
          className="inline-flex items-center gap-2 rounded-full border border-accent-500/40 bg-slate-900/60 px-4 py-2 text-sm text-accent-100 transition-colors hover:border-accent-400 hover:bg-accent-500/10 hover:text-white"
        >
          <ArrowLeftIcon className="h-4 w-4" aria-hidden />
          Back to Directory
        </Link>
      </div>
    );
  }

  const {
    name,
    url,
    categories = [],
    categorySlugs = [],
    lastUpdated,
    overview,
    riskLevel,
    riskSignals = [],
    recommendations = [],
    tosSummary = [],
    privacySummary = [],
    tosDetails = {},
    privacyDetails = {},
    dataHighlights = {},
  } = site;

  const riskStyles = getRiskStyles(riskLevel);

  return (
    <div className="space-y-12">
      <Link
        to="/directory"
        className="inline-flex items-center gap-2 text-sm text-slate-400 transition-colors hover:text-accent-200"
      >
        <ArrowLeftIcon className="h-4 w-4" aria-hidden />
        Back to Directory
      </Link>

      <section className="relative overflow-hidden rounded-3xl border border-slate-800/70 bg-slate-900/70 p-8">
        <div className="pointer-events-none absolute -left-24 top-0 h-64 w-64 rounded-full bg-accent-500/15 blur-3xl" />
        <div className="pointer-events-none absolute -right-24 bottom-0 h-64 w-64 rounded-full bg-highlight-400/10 blur-3xl" />
        <div className="relative space-y-6">
          <header className="space-y-4">
            <div className="flex flex-wrap items-center gap-3">
              <span
                className={`inline-flex items-center gap-2 rounded-full border px-3 py-1 text-xs font-semibold uppercase tracking-wide ${riskStyles.badge}`}
              >
                <span className="h-2 w-2 rounded-full bg-current" />
                {riskLevel ?? 'Unrated'} risk
              </span>
              <span className="rounded-full border border-accent-500/30 bg-slate-900/60 px-3 py-1 text-xs text-accent-100">
                Updated {new Date(lastUpdated).toLocaleDateString()}
              </span>
            </div>
            <h1 className="text-3xl font-bold text-white sm:text-4xl">{name}</h1>
            <a
              href={url}
              target="_blank"
              rel="noreferrer"
              className="inline-flex items-center text-sm text-accent-300 transition-colors hover:text-accent-200"
            >
              {url}
            </a>
          </header>

          {overview && <p className="max-w-3xl text-base text-slate-300">{overview}</p>}

          <div className="flex flex-wrap items-center gap-3">
            {categories.map((category, index) => (
              <Link
                key={category}
                to={`/categories/${categorySlugs[index] ?? slugify(category)}`}
                className="rounded-full border border-slate-800 bg-slate-950/60 px-3 py-1 text-xs text-slate-300 transition-colors hover:border-accent-400 hover:text-white"
              >
                {category}
              </Link>
            ))}
          </div>

          <div className="grid gap-4 sm:grid-cols-3">
            <QuickStat label="Primary sectors" value={categories.join(', ') || 'General'} />
            <QuickStat
              label="Risk signals tracked"
              value={`${riskSignals.length} ${riskSignals.length === 1 ? 'item' : 'items'}`}
            />
            <QuickStat
              label="Recommendations logged"
              value={`${recommendations.length} ${recommendations.length === 1 ? 'action' : 'actions'}`}
            />
          </div>
        </div>
      </section>

      <nav className="flex gap-3 border-b border-slate-800 pb-2">
        {SECTION_CONFIG.map((item) => (
          <NavLink
            key={item.key}
            to={item.key === 'tos' ? `/sites/${site.slug}` : `/sites/${site.slug}/privacy`}
            end={item.key === 'tos'}
            className={({ isActive }) =>
              `rounded-full px-5 py-2 text-sm font-semibold transition-all duration-200 ${
                isActive
                  ? 'bg-accent-500/20 text-white shadow-[0_0_15px_rgba(20,184,166,0.25)]'
                  : 'text-slate-400 hover:bg-slate-900/60 hover:text-accent-100'
              }`
            }
          >
            {item.label}
          </NavLink>
        ))}
      </nav>

      {activeSection === 'privacy' ? (
        <PrivacyView
          summary={privacySummary}
          details={privacyDetails}
          dataHighlights={dataHighlights}
          riskSignals={riskSignals}
          recommendations={recommendations}
        />
      ) : (
        <TosView
          summary={tosSummary}
          details={tosDetails}
          riskSignals={riskSignals}
          recommendations={recommendations}
        />
      )}
    </div>
  );
}

function TosView({ summary, details, riskSignals, recommendations }) {
  const sections = buildDetailSections(details, TOS_SECTIONS);

  return (
    <div className="space-y-10">
      <PolicyList title="Terms of Service highlights" items={summary} />

      <DetailGrid title="Clause deep dive" sections={sections} />

      <section className="grid gap-8 md:grid-cols-2">
        <InsightList
          title="Risk signals to note"
          items={riskSignals}
          emptyMessage="No risk signals recorded yet."
        />
        <InsightList
          title="Recommended follow-up"
          items={recommendations}
          emptyMessage="No specific recommendations at this time."
        />
      </section>
    </div>
  );
}

function PrivacyView({ summary, details, dataHighlights, riskSignals, recommendations }) {
  const sections = buildDetailSections(details, PRIVACY_SECTIONS);

  return (
    <div className="space-y-10">
      <PolicyList title="Privacy Policy highlights" items={summary} />

      <DataHighlights dataHighlights={dataHighlights} />

      <DetailGrid title="Data practices overview" sections={sections} />

      <section className="grid gap-8 md:grid-cols-2">
        <InsightList
          title="Risk signals to note"
          items={riskSignals}
          emptyMessage="No risk signals recorded yet."
        />
        <InsightList
          title="Recommended follow-up"
          items={recommendations}
          emptyMessage="No specific recommendations at this time."
        />
      </section>
    </div>
  );
}

function PolicyList({ title, items = [] }) {
  return (
    <div className="rounded-2xl border border-slate-800/80 bg-slate-900/60 p-8">
      <h2 className="text-lg font-semibold text-white">{title}</h2>
      <ul className="mt-4 list-disc space-y-3 pl-5 text-sm text-slate-300">
        {items.map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
    </div>
  );
}

function DetailGrid({ title, sections = [] }) {
  if (sections.length === 0) {
    return null;
  }
  return (
    <section className="space-y-6">
      <header className="space-y-2">
        <h2 className="text-2xl font-semibold text-white">{title}</h2>
        <p className="text-sm text-slate-400">
          A closer look at the specific clauses and statements found in the document.
        </p>
      </header>
      <div className="grid gap-6 md:grid-cols-2">
        {sections.map((section) => (
          <article
            key={section.label}
            className="rounded-2xl border border-slate-800/70 bg-slate-950/60 p-6 text-sm text-slate-300"
          >
            <h3 className="text-base font-semibold text-white">{section.label}</h3>
            <ul className="mt-3 space-y-2 text-sm text-slate-300">
              {section.items.map((item, index) => (
                <li
                  key={index}
                  className="rounded-xl border border-slate-800/80 bg-slate-900/80 px-4 py-2 text-sm text-slate-200"
                >
                  {item}
                </li>
              ))}
            </ul>
          </article>
        ))}
      </div>
    </section>
  );
}

function InsightList({ title, items = [], emptyMessage }) {
  return (
    <div className="rounded-2xl border border-slate-800/80 bg-slate-900/60 p-8">
      <h2 className="text-lg font-semibold text-white">{title}</h2>
      {items.length === 0 ? (
        <p className="mt-3 text-sm text-slate-400">{emptyMessage}</p>
      ) : (
        <ul className="mt-4 space-y-3 text-sm text-slate-300">
          {items.map((item, index) => (
            <li
              key={index}
              className="rounded-xl border border-slate-800/80 bg-slate-950/60 px-4 py-3 text-sm text-slate-200"
            >
              {item}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}

function QuickStat({ label, value }) {
  return (
    <div className="rounded-2xl border border-slate-800/70 bg-slate-950/60 px-4 py-4 text-sm text-slate-300">
      <p className="text-xs uppercase tracking-wide text-slate-500">{label}</p>
      <p className="mt-2 text-sm text-slate-200">{value}</p>
    </div>
  );
}

function DataHighlights({ dataHighlights }) {
  if (!dataHighlights.collection && !dataHighlights.sharing && !dataHighlights.retention) {
    return null;
  }

  return (
    <section className="rounded-3xl border border-slate-800/70 bg-slate-900/60 p-8">
      <h2 className="text-xl font-semibold text-white">Data handling highlights</h2>
      <p className="mt-2 text-sm text-slate-400">
        Key takeaways from the privacy disclosures. Use these notes to inform vendor risk assessments or
        procurement reviews.
      </p>
      <div className="mt-6 grid gap-4 md:grid-cols-3">
        {dataHighlights.collection && (
          <HighlightCard title="Collection" description={dataHighlights.collection} />
        )}
        {dataHighlights.sharing && (
          <HighlightCard title="Sharing" description={dataHighlights.sharing} />
        )}
        {dataHighlights.retention && (
          <HighlightCard title="Retention" description={dataHighlights.retention} />
        )}
      </div>
    </section>
  );
}

function HighlightCard({ title, description }) {
  return (
    <div className="rounded-2xl border border-slate-800/70 bg-slate-950/60 px-4 py-5 text-sm text-slate-300">
      <p className="text-xs uppercase tracking-wide text-slate-500">{title}</p>
      <p className="mt-2 text-sm text-slate-200">{description}</p>
    </div>
  );
}

function buildDetailSections(details, config) {
  return config
    .map((section) => ({
      label: section.label,
      items: ensureArray(details[section.key]),
    }))
    .filter((section) => section.items.length > 0);
}

function ensureArray(value) {
  if (!value) {
    return [];
  }
  if (Array.isArray(value)) {
    return value;
  }
  return [value];
}

function getRiskStyles(level) {
  switch (String(level).toLowerCase()) {
    case 'elevated':
      return { badge: 'border-highlight-500/50 bg-highlight-500/10 text-highlight-200' };
    case 'moderate':
      return { badge: 'border-accent-500/50 bg-accent-500/10 text-accent-200' };
    case 'low':
      return { badge: 'border-emerald-500/40 bg-emerald-500/10 text-emerald-200' };
    default:
      return { badge: 'border-slate-700 bg-slate-800/80 text-slate-200' };
  }
}
