import { useSearch } from '../context/SearchContext';
import SiteCard from './SiteCard';

export default function SearchResults({ limit = null }) {
  const { query, results } = useSearch();
  const hasQuery = query.trim().length > 0;
  const sites = typeof limit === 'number' ? results.slice(0, limit) : results;

  if (!hasQuery) {
    return null;
  }

  return (
    <section aria-live="polite" className="mt-8 space-y-6">
      <header>
        <h2 className="text-lg font-semibold text-white">
          {sites.length === 0 ? 'No matching policies' : `Found ${sites.length} matching site${sites.length === 1 ? '' : 's'}`}
        </h2>
        <p className="text-sm text-slate-400">Results update automatically as you type.</p>
      </header>
      {sites.length === 0 ? (
        <p className="rounded-lg border border-dashed border-slate-800/80 bg-slate-900/40 p-6 text-sm text-slate-400">
          Try another keyword, company name, or paste a website domain.
        </p>
      ) : (
        <div className="grid gap-6 md:grid-cols-2">
          {sites.map((site) => (
            <SiteCard key={site.slug} site={site} />
          ))}
        </div>
      )}
    </section>
  );
}
