import { useMemo } from 'react';
import { Link, useParams } from 'react-router-dom';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';
import { getCategory, getSitesByCategory } from '../data/sites';
import SiteCard from '../components/SiteCard';

export default function CategoryDetailPage() {
  const { categorySlug } = useParams();
  const category = useMemo(() => getCategory(categorySlug), [categorySlug]);
  const sites = useMemo(() => getSitesByCategory(categorySlug), [categorySlug]);

  if (!category) {
    return (
      <div className="space-y-8">
        <Link
          to="/categories"
          className="inline-flex items-center gap-2 text-sm text-slate-400 transition-colors hover:text-accent-200"
        >
          <ArrowLeftIcon className="h-4 w-4" aria-hidden />
          Back to categories
        </Link>
        <header className="space-y-3">
          <h1 className="text-3xl font-semibold text-white">Category not found</h1>
          <p className="text-sm text-slate-400">
            We couldn&apos;t find that category. Try browsing the full list of categories instead.
          </p>
        </header>
      </div>
    );
  }

  const riskBreakdown = buildRiskBreakdown(sites);
  const mostRecentUpdate = getMostRecentUpdate(sites);

  return (
    <div className="space-y-12">
      <header className="space-y-5">
        <Link
          to="/categories"
          className="inline-flex items-center gap-2 text-sm text-slate-400 transition-colors hover:text-accent-200"
        >
          <ArrowLeftIcon className="h-4 w-4" aria-hidden />
          Back to categories
        </Link>
        <div className="space-y-3 rounded-3xl border border-slate-800/70 bg-slate-900/70 p-8">
          <div className="flex flex-wrap items-center justify-between gap-4">
            <div>
              <p className="inline-flex items-center gap-2 text-sm font-semibold uppercase tracking-wide text-accent-300">
                Category
                <span className="h-px w-8 bg-accent-400/60" />
              </p>
              <h1 className="text-3xl font-bold text-white sm:text-4xl">{category.name}</h1>
            </div>
            <div className="flex flex-col items-end gap-2 text-xs uppercase tracking-wide text-slate-400">
              <span className="rounded-full border border-accent-500/40 bg-accent-500/10 px-3 py-1 text-accent-100">
                {category.count} mapped {category.count === 1 ? 'site' : 'sites'}
              </span>
              {mostRecentUpdate && (
                <span>
                  Latest update:{' '}
                  <strong className="text-accent-200">{mostRecentUpdate.toLocaleDateString()}</strong>
                </span>
              )}
            </div>
          </div>
          <p className="max-w-3xl text-sm text-slate-300">{category.description}</p>
          <br></br>
        <div className="mt-6 grid gap-4 sm:grid-cols-3">
          {riskBreakdown.map((item) => (
            <div
              key={item.level}
              className="rounded-2xl border border-slate-800 bg-slate-900/70 px-4 py-4 text-sm text-slate-300"
            >
              <p className="text-xs uppercase tracking-wide text-slate-500">Risk level</p>
              <p className="mt-1 text-lg font-semibold text-white">{item.level}</p>
              <p className="mt-2 text-2xl font-bold text-accent-200">{item.count}</p>
              <p className="text-xs text-slate-500">summaries</p>
            </div>
          ))}
        </div>
        </div>
      </header>

      <section className="space-y-6">
        <div className="flex flex-col gap-3 sm:flex-row sm:items-baseline sm:justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-white">Summaries in this category</h2>
            <p className="text-sm text-slate-400">
              Each card highlights the most important clauses and privacy signals for the service.
            </p>
          </div>
        </div>

        {sites.length === 0 ? (
          <p className="rounded-2xl border border-dashed border-slate-800/70 bg-slate-900/40 p-10 text-sm text-slate-400">
            We&apos;re still compiling summaries for this category. Check back soon or suggest a
            product to cover next.
          </p>
        ) : (
          <div className="grid gap-6 md:grid-cols-2">
            {sites.map((site) => (
              <SiteCard key={site.slug} site={site} />
            ))}
          </div>
        )}
      </section>
    </div>
  );
}

function buildRiskBreakdown(sites) {
  const totals = sites.reduce((acc, site) => {
    const level = site.riskLevel ?? 'Unrated';
    acc[level] = (acc[level] ?? 0) + 1;
    return acc;
  }, {});

  return Object.entries(totals)
    .map(([level, count]) => ({
      level,
      count,
    }))
    .sort((a, b) => b.count - a.count);
}

function getMostRecentUpdate(sites) {
  if (sites.length === 0) {
    return null;
  }
  const timestamps = sites
    .map((site) => new Date(site.lastUpdated))
    .filter((date) => !Number.isNaN(date.getTime()));
  if (timestamps.length === 0) {
    return null;
  }
  return new Date(Math.max(...timestamps.map((date) => date.getTime())));
}
