const rawSites = await (await fetch('/data/sites.json')).json();

export function slugify(value = '') {
  return value
    .toString()
    .trim()
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, '-')
    .replace(/(^-|-$)+/g, '');
}

const sites = rawSites.map((site) => {
  const categories = (site.categories ?? []).map((category) => category.trim());
  return {
    ...site,
    slug: site.slug ?? slugify(site.name ?? site.url),
    categories,
    categorySlugs: categories.map((category) => slugify(category)),
  };
});

const categoryIndex = buildCategoryIndex(sites);
const categoryList = Array.from(categoryIndex.values()).sort((a, b) =>
  a.name.localeCompare(b.name)
);

export function getSites() {
  return sites;
}

export function getSite(slug) {
  return sites.find((site) => site.slug === slug);
}

export function searchSites(query) {
  if (!query) {
    return sites;
  }

  const normalized = query.trim().toLowerCase();
  return sites.filter((site) => {
    const haystacks = [
      site.name,
      site.url,
      ...(site.domains ?? []),
      ...(site.categories ?? []),
      ...(site.tosSummary ?? []),
      ...(site.privacySummary ?? []),
      ...(site.notablePoints ?? []),
      site.overview,
      site.riskLevel,
      ...(site.riskSignals ?? []),
      ...(site.recommendations ?? []),
      site.dataHighlights?.collection,
      site.dataHighlights?.sharing,
      site.dataHighlights?.retention,
      ...flattenObjectValues(site.tosDetails),
      ...flattenObjectValues(site.privacyDetails),
    ]
      .join(' ')
      .toLowerCase();
    return haystacks.includes(normalized);
  });
}

export function getCategories() {
  return categoryList;
}

export function getCategory(slug) {
  return categoryIndex.get(slug);
}

export function getSitesByCategory(slug) {
  const category = categoryIndex.get(slug);
  if (!category) {
    return [];
  }
  return category.siteSlugs.map((siteSlug) => getSite(siteSlug)).filter(Boolean);
}

function buildCategoryIndex(allSites) {
  const map = new Map();

  allSites.forEach((site) => {
    site.categories.forEach((categoryName) => {
      const slug = slugify(categoryName);
      if (!map.has(slug)) {
        map.set(slug, {
          slug,
          name: categoryName,
          count: 0,
          siteSlugs: [],
          description: createCategoryDescription(categoryName),
        });
      }
      const categoryEntry = map.get(slug);
      categoryEntry.count += 1;
      categoryEntry.siteSlugs.push(site.slug);
    });
  });

  return map;
}

function createCategoryDescription(name) {
  return `Summaries for products focused on ${name.toLowerCase()} — explore how these services handle data, terms, and user rights.`;
}

function flattenObjectValues(value) {
  if (!value) {
    return [];
  }
  if (Array.isArray(value)) {
    return value.flatMap((entry) => flattenObjectValues(entry));
  }
  if (typeof value === 'object') {
    return Object.values(value).flatMap((entry) => flattenObjectValues(entry));
  }
  return [String(value)];
} 