import { Link } from 'react-router-dom';

const riskStyles = {
  low: 'border-emerald-500/40 bg-emerald-500/10 text-emerald-200',
  moderate: 'border-accent-500/50 bg-accent-500/10 text-accent-200',
  elevated: 'border-highlight-500/50 bg-highlight-500/10 text-highlight-200',
  default: 'border-slate-700 bg-slate-800/80 text-slate-200',
};

export default function SiteCard({ site }) {
  const {
    slug,
    name,
    url,
    lastUpdated,
    categories = [],
    riskLevel,
    overview,
  } = site;

  const riskClass = riskStyles[String(riskLevel).toLowerCase()] ?? riskStyles.default;

  return (
    <article className="group relative overflow-hidden rounded-2xl border border-slate-800/70 bg-slate-900/70 p-6 transition-all duration-300 hover:-translate-y-1 hover:border-accent-400/70 hover:bg-slate-900/90">
      <div className="pointer-events-none absolute inset-0 opacity-0 transition-opacity duration-300 group-hover:opacity-100">
        <div className="absolute -right-4 top-1/2 h-32 w-32 -translate-y-1/2 rounded-full bg-accent-500/10 blur-3xl" />
        <div className="absolute -left-6 -top-6 h-24 w-24 rounded-full bg-highlight-400/10 blur-3xl" />
      </div>
      <div className="flex flex-wrap items-start justify-between gap-3">
        <div>
          <h3 className="text-lg font-semibold text-white">
            <Link to={`/products/${slug}`} className="hover:text-accent-200">
              {name}
            </Link>
          </h3>
          <a
            href={url}
            target="_blank"
            rel="noreferrer"
            className="text-sm text-accent-300 hover:text-accent-200"
          >
            {url}
          </a>
        </div>
        <div className="flex flex-col items-end gap-2 text-xs uppercase tracking-wide text-slate-500">
          <span className={`inline-flex items-center gap-2 rounded-full border px-3 py-1 ${riskClass}`}>
            <span className="h-1.5 w-1.5 rounded-full bg-current" />
            {riskLevel ?? 'Unrated'}
          </span>
          <span>Updated {new Date(lastUpdated).toLocaleDateString()}</span>
        </div>
      </div>
      {overview && <p className="mt-3 text-sm text-slate-300">{overview}</p>}
      {categories.length > 0 && (
        <div className="mt-4 flex flex-wrap gap-2">
          {categories.map((category) => (
            <span
              key={category}
              className="rounded-full border border-slate-800 bg-slate-950/60 px-3 py-1 text-xs font-medium text-slate-300"
            >
              {category}
            </span>
          ))}
        </div>
      )}
      <div className="mt-6 flex flex-wrap gap-3 text-sm">
        <Link
          to={`/products/${slug}`}
          className="inline-flex items-center gap-2 rounded-full border border-accent-500/40 bg-accent-500/10 px-3 py-1.5 font-semibold text-accent-100 transition-colors hover:border-accent-400 hover:text-white"
        >
          Product profile
        </Link>
        <Link
          to={`/sites/${slug}`}
          className="inline-flex items-center gap-2 rounded-full border border-slate-700/80 bg-slate-950/70 px-3 py-1.5 font-semibold text-slate-300 transition-colors hover:border-highlight-400 hover:text-white"
        >
          Policy overview
        </Link>
      </div>
    </article>
  );
}
