import { Link, NavLink } from 'react-router-dom';
import SearchBar from './SearchBar';
import navigationLinks from './navigationLinks';

function NavItem({ item }) {
  const content = (
    <span className="flex items-center gap-3">
      <item.icon className="h-5 w-5 text-accent-200 transition group-hover:text-accent-100" aria-hidden />
      <span>{item.label}</span>
    </span>
  );

  if (item.external) {
    return (
      <a
        href={item.to}
        target="_blank"
        rel="noreferrer"
        className="group flex items-center gap-3 rounded-2xl border border-transparent px-4 py-3 text-sm font-semibold text-slate-300 transition-all duration-200 hover:border-accent-500/40 hover:bg-slate-900/70 hover:text-accent-100"
      >
        {content}
      </a>
    );
  }

  return (
    <NavLink
      to={item.to}
      className={({ isActive }) =>
        `group flex items-center gap-3 rounded-2xl border px-4 py-3 text-sm font-semibold transition-all duration-200 ${
          isActive
            ? 'border-accent-500/50 bg-accent-500/15 text-white shadow-[0_12px_30px_rgba(20,184,166,0.25)]'
            : 'border-transparent text-slate-300 hover:border-accent-500/40 hover:bg-slate-900/70 hover:text-accent-100'
        }`
      }
    >
      {content}
    </NavLink>
  );
}

export default function SideNavigation() {
  return (
    <aside className="sticky top-0 hidden h-screen w-72 flex-col border-r border-slate-800/70 bg-slate-950/90 lg:flex xl:w-80">
      <div className="flex-1 overflow-y-auto px-6 py-10">
        <div className="space-y-8">
          <Link to="/" className="flex items-center gap-3 text-2xl font-semibold tracking-tight text-white">
            Policytics
            <span className="rounded-full border border-accent-500/60 bg-accent-500/10 px-3 py-1 text-xs font-medium uppercase tracking-wider text-accent-200">
              MVP
            </span>
          </Link>

          <div className="space-y-4">
            <p className="text-xs leading-relaxed text-slate-400">
              Policytics summarise Terms of Services and privacy policies to make policies more accessible and easier to understand for everyone, and understand about their rights on services we use. 
            </p>
            <SearchBar size="base" placeholder="Search the library…" />
          </div>

          <nav className="space-y-1 pb-2">
            {navigationLinks.map((item) => (
              <NavItem key={item.label} item={item} />
            ))}
          </nav>
        </div>
      </div>

      <div className="border-t border-slate-800/70 px-6 py-6">
        <div className="rounded-2xl border border-slate-800/60 bg-slate-900/50 p-4 text-xs text-slate-400">
          <p className="font-semibold uppercase tracking-wide text-slate-300">Need a custom review?</p>
          <p className="mt-2 leading-relaxed">
            Reach out via the team page to request bespoke monitoring, redlines, or onboarding briefings tailored to your org.
          </p>
          <Link
            to="/team"
            className="mt-3 inline-flex items-center gap-2 text-sm font-semibold text-accent-200 transition-colors hover:text-accent-100"
          >
            Meet the analysts →
          </Link>
        </div>
      </div>
    </aside>
  );
}
