import { useId } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { MagnifyingGlassIcon } from '@heroicons/react/24/outline';
import { useSearch } from '../context/SearchContext';

const sizeClasses = {
  sm: 'h-10 text-sm pl-10 pr-4',
  base: 'h-12 text-base pl-12 pr-4',
  lg: 'h-14 text-lg pl-14 pr-6',
};

export default function SearchBar({
  placeholder = 'Search by website, keyword, or category…',
  size = 'base',
    autoFocus = false,
  }) {
  const { query, setQuery } = useSearch();
  const navigate = useNavigate();
  const location = useLocation();
  const id = useId();
  const inputClasses = sizeClasses[size] ?? sizeClasses.base;

  const handleChange = (event) => {
    const value = event.target.value;
    setQuery(value);

    const trimmed = value.trim();
    if (trimmed.length > 0 && location.pathname !== '/directory') {
      navigate('/directory');
    }
  };

  return (
    <div className="relative w-full">
      <MagnifyingGlassIcon
        aria-hidden
        className="pointer-events-none absolute left-4 top-1/2 h-5 w-5 -translate-y-1/2 text-accent-300"
      />
      <label htmlFor={id} className="sr-only">
        Search Products
      </label>
      <input
        id={id}
        type="search"
        value={query}
        onChange={handleChange}
        placeholder={placeholder}
        autoFocus={autoFocus}
        className={`w-full rounded-full border border-slate-800/70 bg-slate-950/70 text-slate-100 placeholder:text-slate-500 shadow-lg shadow-accent-900/30 focus:border-accent-400 focus:outline-none focus:ring-2 focus:ring-accent-500/30 ${inputClasses}`}
      />
    </div>
  );
}
