import { NavLink } from 'react-router-dom';
import navigationLinks from './navigationLinks';

function MobileNavItem({ item }) {
  if (item.external) {
    return (
      <a
        key={item.label}
        href={item.to}
        target="_blank"
        rel="noreferrer"
        className="flex flex-col items-center gap-1 rounded-xl px-3 py-2 text-slate-400 transition-colors hover:text-accent-100"
      >
        <item.icon className="h-5 w-5" aria-hidden />
        <span>{item.label}</span>
      </a>
    );
  }

  return (
    <NavLink
      key={item.label}
      to={item.to}
      className={({ isActive }) =>
        `flex flex-col items-center gap-1 rounded-xl px-3 py-2 transition-colors ${
          isActive ? 'text-accent-100' : 'text-slate-400 hover:text-accent-100'
        }`
      }
    >
      <item.icon className="h-5 w-5" aria-hidden />
      <span>{item.label}</span>
    </NavLink>
  );
}

export default function MobileNavigation() {
  return (
    <nav className="fixed inset-x-0 bottom-0 z-40 border-t border-slate-800/80 bg-slate-950/95 backdrop-blur supports-[backdrop-filter]:bg-slate-950/80 lg:hidden">
      <div className="mx-auto flex max-w-3xl items-center justify-around px-4 py-3 text-xs font-medium text-slate-300">
        {navigationLinks.map((item) => (
          <MobileNavItem key={item.label} item={item} />
        ))}
      </div>
    </nav>
  );
}
