import { Link, useParams } from 'react-router-dom';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';
import { getSite, slugify } from '../data/sites';

export default function ProductDetailPage() {
  const { slug } = useParams();
  const site = getSite(slug);

  if (!site) {
    return (
      <div className="space-y-8">
        <header className="space-y-3">
          <h1 className="text-3xl font-semibold text-white">Product not found</h1>
          <p className="text-sm text-slate-400">
            We couldn&apos;t find a product profile for that slug yet. Try browsing the directory instead.
          </p>
        </header>
        <Link
          to="/directory"
          className="inline-flex items-center gap-2 rounded-full border border-accent-500/40 bg-slate-900/60 px-4 py-2 text-sm text-accent-100 transition-colors hover:border-accent-400 hover:bg-accent-500/10 hover:text-white"
        >
          <ArrowLeftIcon className="h-4 w-4" aria-hidden />
          Back to directory
        </Link>
      </div>
    );
  }

  const {
    name,
    url,
    overview,
    about,
    categories = [],
    categorySlugs = [],
    lastUpdated,
    riskLevel,
    features = [],
    productHighlights = [],
    integrations = [],
    support = [],
    pricing,
    pricingNotes,
    dataHighlights = {},
    notablePoints = [],
  } = site;

  const aboutCopy = about ?? overview ?? 'Add an about description for this product in data/sites.json.';
  const featureList =
    features.length > 0
      ? features
      : Array.isArray(site.tosSummary)
      ? site.tosSummary.slice(0, 4)
      : [];
  const highlightList =
    productHighlights.length > 0
      ? productHighlights
      : Array.isArray(site.privacySummary)
      ? site.privacySummary.slice(0, 4)
      : notablePoints;
  const integrationsList = integrations.length > 0 ? integrations : [];
  const supportChannels = Array.isArray(support) ? support : [];
  const pricingCopy =
    pricing ??
    pricingNotes ??
    'Add pricing guidance (plans, tiers, billing cadence) in data/sites.json under "pricing" or "pricingNotes".';

  return (
    <div className="space-y-12">
      <Link
        to="/directory"
        className="inline-flex items-center gap-2 text-sm text-slate-400 transition-colors hover:text-accent-200"
      >
        <ArrowLeftIcon className="h-4 w-4" aria-hidden />
        Back to directory
      </Link>

      <section className="relative overflow-hidden rounded-3xl border border-slate-800/70 bg-slate-950/90 p-8 shadow-[0_25px_80px_rgba(8,47,73,0.45)]">
        <div className="pointer-events-none absolute -left-20 top-0 h-72 w-72 rounded-full bg-accent-500/20 blur-3xl" />
        <div className="pointer-events-none absolute -right-28 bottom-0 h-80 w-80 rounded-full bg-highlight-400/20 blur-3xl" />
        <div className="relative space-y-6">
          <div className="flex flex-wrap items-start justify-between gap-6">
            <div className="space-y-3">
              <span className="inline-flex items-center gap-2 rounded-full border border-accent-400/40 bg-accent-500/15 px-3 py-1 text-xs font-semibold uppercase tracking-wide text-accent-100">
                Product insight
              </span>
              <h1 className="text-4xl font-bold text-white sm:text-5xl">{name}</h1>
              <a
                href={url}
                target="_blank"
                rel="noreferrer"
                className="inline-flex items-center gap-2 text-sm text-accent-200 transition-colors hover:text-accent-100"
              >
                {url}
              </a>
            </div>
            <div className="flex flex-col items-end gap-2">
              <span className="rounded-full border border-accent-500/30 bg-slate-900/60 px-3 py-1 text-xs text-accent-100">
                Updated {new Date(lastUpdated).toLocaleDateString()}
              </span>
              <span className="rounded-full border border-slate-700/80 bg-slate-900/70 px-3 py-1 text-xs uppercase tracking-wide text-slate-400">
                {riskLevel ?? 'Risk pending'}
              </span>
            </div>
          </div>

          <p className="max-w-3xl text-base text-slate-200">{aboutCopy}</p>

          {categories.length > 0 && (
            <div className="flex flex-wrap gap-3">
              {categories.map((category, index) => (
                <Link
                  key={category}
                  to={`/categories/${categorySlugs[index] ?? slugify(category)}`}
                  className="rounded-full border border-slate-800 bg-slate-950/70 px-4 py-1.5 text-xs font-semibold uppercase tracking-wide text-slate-300 transition-colors hover:border-accent-400 hover:text-accent-100"
                >
                  {category}
                </Link>
              ))}
            </div>
          )}

          <div className="flex flex-wrap gap-3">
            <Link
              to={`/sites/${slug}`}
              className="inline-flex items-center gap-2 rounded-full border border-accent-500/50 bg-accent-500/10 px-4 py-2 text-sm font-semibold text-accent-100 transition-colors hover:border-accent-400 hover:text-white"
            >
              View policy deep dive
            </Link>
            <Link
              to={`/sites/${slug}/privacy`}
              className="inline-flex items-center gap-2 rounded-full border border-highlight-500/50 bg-highlight-500/10 px-4 py-2 text-sm font-semibold text-highlight-100 transition-colors hover:border-highlight-400 hover:text-white"
            >
              Jump to privacy summary
            </Link>
          </div>
        </div>
      </section>

      <section className="grid gap-10 lg:grid-cols-[1.2fr_1fr]">
        <div className="space-y-6 rounded-3xl border border-slate-800/60 bg-slate-900/60 p-8">
          <header className="space-y-2">
            <p className="text-xs font-semibold uppercase tracking-wide text-accent-200">Product features</p>
            <h2 className="text-xl font-semibold text-white">What the product excels at</h2>
            <p className="text-sm text-slate-400">
              Maintain a list of marquee capabilities to help stakeholders understand positioning at a glance.
            </p>
          </header>
          {featureList.length === 0 ? (
            <p className="rounded-2xl border border-dashed border-slate-800/70 bg-slate-950/50 p-6 text-sm text-slate-400">
              Add a <code>features</code> array in the site data to spotlight the product&apos;s key value props.
            </p>
          ) : (
            <ul className="space-y-3 text-sm text-slate-200">
              {featureList.map((feature, index) => (
                <li
                  key={index}
                  className="flex items-start gap-3 rounded-2xl border border-slate-800/60 bg-slate-950/70 px-5 py-4"
                >
                  <span className="mt-1 h-2 w-2 rounded-full bg-accent-400" />
                  <span>{feature}</span>
                </li>
              ))}
            </ul>
          )}
        </div>

        <aside className="space-y-6 rounded-3xl border border-slate-800/60 bg-slate-900/60 p-8">
          <header className="space-y-1">
            <p className="text-xs font-semibold uppercase tracking-wide text-accent-200">Snapshot</p>
            <h2 className="text-lg font-semibold text-white">Commercial overview</h2>
          </header>
          <div className="space-y-4 text-sm text-slate-300">
            <div>
              <p className="text-xs uppercase tracking-wide text-slate-500">Pricing</p>
              <p className="mt-1 text-slate-200">{pricingCopy}</p>
            </div>
            <div>
              <p className="text-xs uppercase tracking-wide text-slate-500">Integrations &amp; ecosystem</p>
              {integrationsList.length === 0 ? (
                <p className="mt-1 text-slate-400">
                  Add an <code>integrations</code> array in the site data to highlight supported platforms.
                </p>
              ) : (
                <ul className="mt-1 list-disc space-y-1 pl-5">
                  {integrationsList.map((integration, index) => (
                    <li key={index}>{integration}</li>
                  ))}
                </ul>
              )}
            </div>
            <div>
              <p className="text-xs uppercase tracking-wide text-slate-500">Support channels</p>
              {supportChannels.length === 0 ? (
                <p className="mt-1 text-slate-400">
                  Add a <code>support</code> array in the site data with items like &ldquo;Live chat&rdquo; or &ldquo;Dedicated CSM&rdquo;.
                </p>
              ) : (
                <ul className="mt-1 list-disc space-y-1 pl-5">
                  {supportChannels.map((channel, index) => (
                    <li key={index}>{channel}</li>
                  ))}
                </ul>
              )}
            </div>
          </div>
        </aside>
      </section>

      <section className="space-y-8 rounded-3xl border border-slate-800/60 bg-slate-900/60 p-8">
        <header className="space-y-2">
          <p className="text-xs font-semibold uppercase tracking-wide text-accent-200">Key takeaways</p>
          <h2 className="text-xl font-semibold text-white">What to know before adopting</h2>
          <p className="text-sm text-slate-400">
            Capture positioning statements, roadmap commitments, or differentiators so the policy context has business context.
          </p>
        </header>
        {highlightList.length === 0 ? (
          <p className="rounded-2xl border border-dashed border-slate-800/70 bg-slate-950/50 p-6 text-sm text-slate-400">
            Add a <code>productHighlights</code> array (or populate <code>privacySummary</code>) to surface key talking points.
          </p>
        ) : (
          <div className="grid gap-4 md:grid-cols-2">
            {highlightList.map((item, index) => (
              <article key={index} className="rounded-2xl border border-slate-800/60 bg-slate-950/80 p-5">
                <p className="text-sm text-slate-200">{item}</p>
              </article>
            ))}
          </div>
        )}

        <div className="rounded-2xl border border-slate-800/60 bg-slate-950/80 p-6">
          <p className="text-xs uppercase tracking-wide text-slate-500">Data handling snapshot</p>
          <div className="mt-4 grid gap-4 md:grid-cols-3">
            <DataHighlight label="Collection" value={dataHighlights.collection} />
            <DataHighlight label="Sharing" value={dataHighlights.sharing} />
            <DataHighlight label="Retention" value={dataHighlights.retention} />
          </div>
        </div>
      </section>
    </div>
  );
}

function DataHighlight({ label, value }) {
  return (
    <div className="rounded-2xl border border-slate-800/60 bg-slate-900/70 p-4">
      <p className="text-xs uppercase tracking-wide text-slate-500">{label}</p>
      <p className="mt-2 text-sm text-slate-200">
        {value ?? 'Add details in dataHighlights to summarise this aspect of the product.'}
      </p>
    </div>
  );
}
