import { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { getCategories } from '../data/sites';

export default function CategoriesPage() {
  const categories = useMemo(() => getCategories(), []);

  return (
    <div className="space-y-10">
      <header className="space-y-3">
        <p className="inline-flex items-center gap-2 text-sm font-semibold uppercase tracking-wide text-accent-300">
          Categories
          <span className="h-px w-8 bg-accent-400/60" />
        </p>
        <h1 className="text-3xl font-bold text-white sm:text-4xl">Browse by Domains</h1>
        <p className="max-w-3xl text-sm text-slate-400">
          Quickly scan summaries grouped by industry or product type. Each category highlights how
          companies in that space handle billing, data collection, and policy changes.
        </p>
      </header>

      <div className="grid gap-6 md:grid-cols-2">
        {categories.map((category) => (
          <CategoryCard key={category.slug} category={category} />
        ))}
      </div>
    </div>
  );
}

function CategoryCard({ category }) {
  return (
    <Link
      to={`/categories/${category.slug}`}
      className="group relative flex flex-col gap-4 overflow-hidden rounded-3xl border border-slate-800/70 bg-slate-900/70 p-6 transition-all duration-300 hover:-translate-y-1 hover:border-accent-400/60 hover:bg-slate-900/90"
    >
      <div className="pointer-events-none absolute -right-16 top-0 h-48 w-48 rounded-full bg-accent-500/10 blur-3xl transition-opacity duration-300 group-hover:opacity-100" />
      <div className="flex items-baseline justify-between gap-4">
        <h2 className="text-xl font-semibold text-white">{category.name}</h2>
        <span className="rounded-full border border-accent-500/40 bg-accent-500/10 px-3 py-1 text-xs font-semibold uppercase tracking-wide text-accent-100">
          {category.count} {category.count === 1 ? 'site' : 'sites'}
        </span>
      </div>
      <p className="text-sm text-slate-300">{category.description}</p>
      <span className="mt-auto inline-flex items-center gap-2 text-sm font-medium text-accent-200">
        Explore summaries
        <span aria-hidden className="transition-transform duration-300 group-hover:translate-x-1">
          &rarr;
        </span>
      </span>
    </Link>
  );
}
