import { useState } from 'react';
import { useSearch } from '../context/SearchContext';
import SiteCard from '../components/SiteCard';
import { getCategories } from '../data/sites';

export default function DirectoryPage() {
  const { query, results, sites } = useSearch();
  const categories = getCategories();
  const [activeCategory, setActiveCategory] = useState('all');
  const hasQuery = query.trim().length > 0;
  const baseSites = hasQuery ? results : sites;
  const displaySites =
    activeCategory === 'all'
      ? baseSites
      : baseSites.filter((site) => site.categorySlugs?.includes(activeCategory));
  const activeCategoryLabel =
    activeCategory === 'all'
      ? 'All categories'
      : categories.find((category) => category.slug === activeCategory)?.name ?? 'Selected category';

  return (
    <div className="space-y-10">
      <header className="space-y-3">
        <p className="inline-flex items-center gap-2 text-sm font-semibold uppercase tracking-wide text-accent-300">
          Directory
          <span className="h-px w-8 bg-accent-400/60" />
        </p>
        <div className="flex flex-col gap-3 sm:flex-row sm:items-end sm:justify-between">
          <div>
            <h1 className="text-3xl font-bold text-white sm:text-4xl">All Monitored Products</h1>
            <p className="mt-2 max-w-2xl text-sm text-slate-400">
              {hasQuery
                ? `Showing ${displaySites.length} result${displaySites.length === 1 ? '' : 's'} for “${query}”.`
                : 'Browse and discover online products covered by us, and unveil the summary of terms of services and policy policy of each of the products here. New product and policy coverage lands weekly too.'}
            </p>
          </div>
          <div className="flex flex-col items-start gap-3 sm:flex-row sm:items-center sm:gap-4">
            <div className="rounded-full border border-accent-500/40 bg-accent-500/10 px-4 py-1 text-xs font-semibold uppercase tracking-wide text-accent-100">
              {sites.length} active listings
            </div>
            <label className="flex items-center gap-2 text-xs uppercase tracking-wide text-slate-500">
              Filter by Category
              <div className="relative">
                <select
                  value={activeCategory}
                  onChange={(event) => setActiveCategory(event.target.value)}
                  className="rounded-full border border-slate-700 bg-slate-950/80 px-4 py-1.5 text-sm font-medium text-slate-200 transition-colors hover:border-accent-400 focus:border-accent-400 focus:outline-none"
                >
                  <option value="all">All Categories</option>
                  {categories.map((category) => (
                    <option key={category.slug} value={category.slug}>
                      {category.name}
                    </option>
                  ))}
                </select>
              </div>
            </label>
          </div>
        </div>
      </header>

      {hasQuery && (
        <p className="text-xs uppercase tracking-wide text-slate-500">
          Tip: Search for domains like <span className="text-accent-200">example.com</span> or keywords such as{' '}
          <span className="text-highlight-200">subscription</span>.
        </p>
      )}

      {displaySites.length === 0 ? (
        <p className="rounded-2xl border border-dashed border-slate-800/70 bg-slate-900/40 p-10 text-sm text-slate-400">
          No sites in our database match this selection yet. Please try another keyword or switch categories.
        </p>
      ) : (
        <>
          <p className="text-xs uppercase tracking-wide text-slate-500">
            Viewing {displaySites.length} result{displaySites.length === 1 ? '' : 's'} from {activeCategoryLabel}.
            {activeCategory !== 'all' && (
              <button
                type="button"
                onClick={() => setActiveCategory('all')}
                className="ml-2 inline-flex items-center gap-1 rounded-full border border-slate-700 px-3 py-0.5 text-[10px] font-semibold uppercase tracking-widest text-slate-400 transition hover:border-accent-400 hover:text-accent-100"
              >
                Clear Filter
              </button>
            )}
          </p>
          <div className="grid gap-6 md:grid-cols-2">
            {displaySites.map((site) => (
              <SiteCard key={site.slug} site={site} />
            ))}
          </div>
        </>
      )}
    </div>
  );
}
